}

.docs-transition-demo {
  display: inline-block;
  max-width: 100%;
  margin-bottom: 1rem;
  border: 4px solid #fefefe;
  border-radius: 0;
  box-shadow: 0 0 0 1px rgba(10, 10, 10, 0.2);
  line-height: 0;
  display: block;
  max-width: 400px;
  margin: 0 auto;
  backface-visibility: hidden;
}

#series-example-1 {
  opacity: 0;
  transform: translateY(100%);
  animation-fill-mode: both;
}
.is-animating #series-example-1 {
  animation-name: custom-1;
  animation-delay: 0s;
  animation-duration: 0.5s;
}
@keyframes custom-1 {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
.is-paused #series-example-1 {
  animation-play-state: paused;
}

#series-example-2 {
  opacity: 0;
  transform: rotate(-1turn);
  animation-fill-mode: both;
}
.is-animating #series-example-2 {
  animation-name: custom-2;
  animation-delay: 0.5s;
  animation-duration: 0.5s;
}
@keyframes custom-2 {
  0% {
    opacity: 0;
    transform: rotate(-1turn);
  }
  100% {
    opacity: 1;
    transform: rotate(0);
  }
}
.is-paused #series-example-2 {
  animation-play-state: paused;
}

#series-example-3 {
  opacity: 0;
  transform: perspective(2000px) rotateX(90deg);
  transform-origin: bottom;
  animation-fill-mode: both;
}
.is-animating #series-example-3 {
  animation-name: custom-3;
  animation-delay: 1.5s;
  animation-duration: 0.5s;
}
@keyframes custom-3 {
  0% {
    opacity: 0;
    transform: perspective(2000px) rotateX(90deg);
    transform-origin: bottom;
  }
  100% {
    opacity: 1;
    transform: perspective(2000px) rotate(0deg);
  }
}
.is-paused #series-example-3 {
  animation-play-state: paused;
}

